.header {
    background: linear-gradient(black, grey);
    height: 100px;
}

.header h1 {
    position: relative;
    top: 20px;
    left: 8%;
    font-size: 45px;
    color: white;
}

@media screen and (max-width: 380px) {
    .header h1 {
        font-size: 30px;
        top: 30px;
        left: 5%;
    }
}